.hero {
  font-family: "Oswald", "Arial", sans-serif;
  color: $inverse-text;
  background-color: rgba(104, 183, 56, 0.85);
  position: relative;
  text-align: center;
  box-sizing: border-box;
  padding: 27px 35px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;

  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-image: url("../img/index-hero-background.png");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    z-index: -1;

    @media (min-width: $tablet-width) {
      content: none;
    }
  }

  @media (min-width: $tablet-width) {
    background: none;
    color: $basic-text;
    text-align: start;
    padding: 0 120px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 82px;
    padding: 0 110px 0 190px;
  }
}

.hero__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  margin: 0;

  @media (min-width: $tablet-width) {
    font-size: 60px;
    line-height: 60px;
    width: 450px;
  }

  @media (min-width: $desktop-width) {
    width: 420px;
  }
}

.hero__subtitle {
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 20px;
  }
}

.hero__img {
  @media (min-width: $tablet-width) {
    position: absolute;
    z-index: -1;
  }
}

.hero__button {
  padding: 14px 26px;
  width: max-content;

  @media (min-width: $tablet-width) {
    margin-bottom: 526px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 300px;
  }
}
